@use '../../../styles/base/mixins' as *;

::ng-deep .mat-mdc-slide-toggle .mat-internal-form-field {
  // width: 300px;
  width: 100%;
}

::ng-deep .mdc-label {
  flex-grow: 1;
  font-family: Inter, sans-serif;
  font-size: 16px;
}

.page-container {
  display: flex;
  flex-direction: column;
  border-radius: 16px;

  .settings-wrapper {
    max-width: 700px;
    padding: 16px;
    border-radius: 16px;

    .theme-toggle-wrapper {
      mat-slide-toggle {
        width: 100%;
      }
    }

    .language-wrapper {
      margin-top: 16px;
    }

    .copyright-block {
      position: absolute;
      bottom: 50px;
    }
  }

  @include respond(compact) {
    // padding-right: 16px;
    // padding-bottom: 16px;
    padding: 24px;
    width: 100%;
    max-width: 600px;
    height: fit-content;
    gap: 16px;

    .settings-wrapper {
      width: 100%;
      // max-width: 600px;
      padding: 0;
      order: 1;
    }

    ui-page-toolbar {
      order: 2;
    }
  }
}
